import pa from "@/assets/socsesh4.json";

export default {
  data() {
    return {
      page: 1,
      artist: "",
      songNames: [],
      jsons: [],
      artists: []
    };
  },
  mounted() {
    this.makeJsonList();
    this.makeList();
    this.makeArtistList();
  },
  methods: {
    makeJsonList() {
      // this.jsons.push(p1);
      // this.jsons.push(p2);
      // this.jsons.push(p3);
      // this.jsons.push(p4);
      this.jsons.push(pa);
      // this.jsons.push(p5);
      // this.jsons.push(p6);
      // this.jsons.push(p7);
      // this.jsons.push(p8);
      // this.jsons.push(p9);
      // this.jsons.push(p10);
      // this.jsons.push(p11);
      // this.jsons.push(p12);
      // this.jsons.push(p13);
      // this.jsons.push(p14);
      // this.jsons.push(p15);
      // this.jsons.push(p16);
      // this.jsons.push(p17);
      // this.jsons.push(p18);
      // this.jsons.push(p19);
      // this.jsons.push(p20);
      // this.jsons.push(p21);
    },
    makeList() {
      for (let j = 0; j < this.jsons.length; j++) {
        for (let i = 0; i < this.jsons[j].tracks.items.length; i++) {
          this.songNames.push(this.jsons[j].tracks.items[i].track.name);
        }
      }
    },
    makeArtistList() {
      for (let j = 0; j < this.jsons.length; j++) {
        for (let i = 0; i < this.jsons[j].tracks.items.length; i++) {
          if (this.jsons[j].tracks.items[i].track.artists.length > 0) {
            let arts = [];
            for (
              let k = 0;
              k < this.jsons[j].tracks.items[i].track.artists.length;
              k++
            ) {
              arts.push(
                " " + this.jsons[j].tracks.items[i].track.artists[k].name
              );
            }
            this.artists.push(arts);
          } else {
            this.artists.push(
              this.jsons[j].tracks.items[i].track.artists[0].name
            );
          }
        }
      }
    }
  }
};
